body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  transition: background-color 1s ease-in-out;
}

.fadeout{
  opacity: 0;
}
.fadein{
  opacity: 1;
}

#wrapper{
  display: flex;
  justify-content: center;
  flex-flow: column;
  align-items: center;
  width: 550px;
}

#quote-box {
  background-color: #fff;
  padding: 40px 50px;
  border-radius: 3px;
  display: table;
  text-align: center;
  font-family: Raleway, sans-serif;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

  .quote-text{
    display: inline-block;
    text-align: center;
    font-weight: 500;
    font-size: 1.75em;
    width: 100%;
    height: auto;

    FaQuoteLeft{
      font-size: 1em;
      margin-right: 0.4em;
    }
  }

  .quote-author{
    width: 100%;
    padding-top: 20px;
    text-align: right;
    font-size: 1em;
    display: inline-block;
  }

  .buttons{
    width: 100%;
    display: inline-block;

    .button{
      height: 38px;
      border-radius: 3px;
      font-size: 0.85em;
      padding: 8px 18px 6px 18px;
      margin-top: 30px;
      opacity: 1;
      cursor: pointer;
      color: #fff;
      transition: background-color 1s ease-in-out;
      border: none;
    }
    .button:hover {
      opacity: 0.9;
    }
    .button#tweet-quote, .button#tumblr-quote {
      float: left;
      padding: 0px;
      padding-top: 8px;
      text-align: center;
      font-size: 1.2em;
      margin-right: 5px;
      height: 30px;
      width: 40px;
    }

    .button#new-quote {
      float: right;
    }
  }
}

.footer{
  width: 100%;
  text-align: center;
  margin: 15px auto 30px auto;
  font-size: 0.8rem;
  color: #fff;

  a {
    font-weight: 600;
    text-decoration: none;
    color: #fff;
    font-family: Raleway, sans-serif;
  }
}


@media screen and (max-width: 40em){
  #wrapper{
    width: 300px;
  }
  #text{
    font-size: 80%;
}
  #quote-box{
    padding: 20px 30px;
  }
}